<template>
  <!-- index.wxml -->
  <div class="checkPhone">
    <div class="form">
      <div class="title">请输入报名时的手机号</div>
      <VanField
        title-width="50"
        v-model="phone"
        bind:input="phoneChange"
        center
        clearable
        placeholder="请输入手机号"
        :border="false">
        <div slot="label" class="phone_lable">
          +86
          <text style="font-size: 30px">▾</text>
        </div>
      </VanField>
      <VanButton
        type="primary"
        size="normal"
        block
        class="btn"
        @click="fenban">获取老师微信</VanButton>
      <div class="tips">
        <span>注意：<br />手机号是上课唯一凭证，只有正确输入报名手机号，才能添加班主任老师上课！</span>
      </div>
    </div>
    <VanDialog id="van-dialog" />
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
import Store from '@/store'
import { getSearch, isPhone } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({ getTokenQuery: { ...getSearch() } })
    const { id } = Store.state.user.userInfo || {}
    Store.$log.update({ mid: id })
    next()
  },
  data () {
    return {
      phone: ''
    }
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  created () {
    this.initShareData()
    this.getProductNameList()
  },
  methods: {
    ...mapMutations('user', ['SET_CLASS_INFO', 'SET_CLASS_NAME']),
    initShareData () {
      this.$wx.setShareData({
        title: '微淼卓越管理课分班系统', // 分享标题
        desc: '请务必点此进行分班，添加班主任微信！否则无法进群学习', // 分享描述
        link: location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: 'https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/img/jpklog.jpg' // 分享图标
      })
    },
    async fenban () {
      // 分班 校验手机号
      const phone = this.phone
      if (!phone) {
        return this.$toast('请填写手机号')
      }
      if (!isPhone(phone)) {
        return this.$toast('手机号有误,请重填')
      }
      try {
        const data = await this.$apis.user.fenban({
          version: '1.1',
          product_key: 'PRODUCT_SN_MANAGE_9',
          mid: this.userInfo.id,
          phone
        })
        this.SET_CLASS_INFO(data)
        this.$router.push({
          name: 'ManageTecherWechat'
        })
      } catch (e) {
        console.log(e)
      }
      // 分班
    },
    async getProductNameList () {
      // 获取商品标题
      const data = await this.$apis.user.getProductNameList({ product_key: 'PRODUCT_SN_MANAGE_9' })
      this.SET_CLASS_NAME(data)
    }
  }
}
</script>
<style lang="scss" scoped>
.checkPhone {
  width: 750px;
  background: #fff;
  min-height: 100vh;
  padding-top: 100px;

  .form {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    height: 800px;
  }

  .title {
    width: 750px;
    height: 67px;
    font-size: 48px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #000;
    line-height: 67px;
    text-align: center;
  }

  .tips {
    width: 614px;
    height: 126px;
    font-size: 24px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #bbb;
    line-height: 42px;
    padding-top: 40px;
  }

  /deep/.van-cell {
    width: 614px;
    height: 98px;
    background: #f5f5f5;
    border-radius: 49px;
  }

  /deep/.van-field__label {
    width: 90px;
  }

  .phone_lable {
    font-size: 32px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333;
    width: 100px;
  }

  .btn {
    width: 614px;
    height: 98px;
    background: #1d53df;
    border-radius: 49px;
  }
}
</style>
